<template>
  <v-container>
    <v-toolbar app>
      <v-toolbar-title class="headline text-uppercase">
        <span class="font-weight-light">Flasky.mp3</span>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn color="info" @click="logout()">logout from {{username}}</v-btn>
    </v-toolbar>
    <v-btn class="upload" color="info" @click="upload()">upload</v-btn>
    <v-content v-for="(song, index) in songData" :key="index">
      <v-card color="info" dark>
        <v-card-title>
          <span class="title font-weight-light">{{song.postName}}</span>
        </v-card-title>

        <v-card-text class="headline font-weight-bold">
          <audio controls>
            <!-- <source src="horse.ogg" type="audio/ogg"> -->

            <source id="song" :src="'http://localhost:9000/song/'+song.postName" type="audio/mpeg" />Your browser does not support the audio element.
          </audio>
        </v-card-text>

        <v-card-actions>
          <v-list-tile class="grow">
            <v-list-tile-avatar color="grey darken-3">
              <v-icon>person</v-icon>
            </v-list-tile-avatar>

            <v-list-tile-content>
              <v-list-tile-title>{{song.by}}</v-list-tile-title>
            </v-list-tile-content>

            <v-layout align-center justify-end>
              <!-- <v-icon class="mr-1">arrow</v-icon> -->
              <v-btn
                class="space"
                v-if="song.by==username"
                color="danger"
                @click="delet(song.postName)"
              >remove</v-btn>
              <a :href="'http://localhost:9000/song/'+song.postName" target="_blank">
                <v-btn class="space">Download</v-btn>
              </a>
            </v-layout>
          </v-list-tile>
        </v-card-actions>
      </v-card>
    </v-content>
  </v-container>
</template>

<script>
import HelloWorld from "../components/HelloWorld";

export default {
  data: () => ({
    songData: [],
    username: localStorage.getItem("username")
  }),
  methods: {
    async load() {
      var self = this;
      await fetch("http://localhost:9000/users/" + this.$route.params.id, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + localStorage.getItem("token")
        }
      })
        .then(function(response) {
          console.log(response);
          // Examine the text in the response
          if (response.status == 200)
            response.json().then(function(data) {
              console.log(data);
              self.songData = data.message;
              self.songData.reverse();
            });
          else {
            throw console.error("an error occured");
          }
        })
        .catch(function(err) {
          console.log("Fetch Error :-S", err);
          self.logout();
        });
    },
    upload() {
      this.$router.push({
        name: "upload"
      });
    },
    logout() {
      localStorage.removeItem("token");
      this.$router.push("/login");
    },
    async delet(song) {
      var self = this;
      await fetch("http://localhost:9000/posts/" + song, {
        method: "DELETE",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + localStorage.getItem("token")
        }
      })
        .then(function(response) {
          console.log(response);
          // Examine the text in the response
          if (response.status == 200)
            response.json().then(function(data) {
              console.log(data);
            });
          else {
            throw console.error("an error occured");
          }
        })
        .catch(function(err) {
          console.log("Fetch Error :-S", err);
          //self.logout();
        });
      await this.load();
    }
  },
  async mounted() {
    await this.load();
  }
};
</script>

<style scoped>
.upload {
  position: fixed !important;
  top: 90% !important;
  left: 90% !important;
}
a {
  color: white;
}
.v-card {
  float: left;
  min-width: 80%;
}
.space {
  margin: 6px;
}
</style>

