<template>
  <div id="app">
    <v-app id="inspire">
      <v-content>
        <v-container v-if="loading" fluid fill-height>
          <!-- <div class="text-xs-center"> -->
          <v-layout align-center justify-center>
            <v-progress-circular
              v-if="loading"
              :size="70"
              :width="7"
              color="indigo lighten-1"
              indeterminate
            ></v-progress-circular>
            <!-- </div> -->
          </v-layout>
        </v-container>
        <!-- <div v-if="!loading"> -->
        <v-container v-if="!loading" fluid fill-height>
          <v-layout align-center="align-center" justify-center="justify-center">
            <v-flex class="login-form text-xs-center">
              <v-card light="light">
                <v-card-text>
                  <div class="display-1 mb-3">
                    <!-- <v-icon class="mr-2" large="large">work</v-icon> -->
                    <v-avatar size="65px" color="orange lighten-2">
                      <v-icon x-large color="black">map</v-icon>
                    </v-avatar>
                  </div>
                  <v-form ref="form" v-model="valid" lazy-validation>
                    <v-alert
                      :value="alert"
                      color="error"
                      icon="new_releases"
                    >wrong info or bad connection.</v-alert>
                    <v-alert :value="alert2" color="success" icon="new_releases">created successfuly</v-alert>
                    <v-text-field
                      light="light"
                      prepend-icon="person"
                      v-model="name"
                      type="text"
                      :counter="8"
                      :rules="nameRules"
                      label="Name"
                      required
                    ></v-text-field>
                    <v-text-field
                      light="light"
                      prepend-icon="lock"
                      name="password"
                      label="Password"
                      v-model="pass"
                      id="password"
                      type="password"
                      :counter="10"
                      :rules="passRules"
                      required
                    ></v-text-field>
                    <v-btn
                      color="orange lighten-2"
                      class="black--text"
                      :disabled="!valid"
                      @click="validate"
                    >Sign up</v-btn>
                  </v-form>
                </v-card-text>
              </v-card>
              <div style="margin-top:13px;">
                Don't have an account?
                <router-link :to="{name:'login'}">sign in</router-link>
              </div>
            </v-flex>
          </v-layout>
        </v-container>
        <!-- </div> -->
      </v-content>
    </v-app>
  </div>
</template>

<script>
export default {
  data: () => ({
    valid: true,
    name: "",
    pass: "",
    alert2: false,
    description: "",
    nameRules: [
      v => !!v || "field is required",
      v => (v && v.length <= 10) || "field must be less than 10 characters"
    ],
    noRules: [
      v => !!v || "field is required",
      v => (v && v.length <= 20) || "field cant be more than 20 characters"
    ],
    passRules: [
      v => !!v || "password is required",
      v =>
        (v && v.length <= 8 && v.length >= 6) ||
        "password must be less than 9 characters and longer than 5"
    ],
    alert: false,
    loading: false
  }),

  methods: {
    async validate() {
      var self = this;
      if (this.$refs.form.validate()) {
        self.loading = true;
        self.alert2 = false;
        this.snackbar = true;
        console.log(this.name, this.pass);
        //http://localhost:10000/
        //https://uoitc-test-server.herokuapp.com/
        await fetch("http://localhost:9000/register", {
          method: "POST",
          headers: {
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            password: "" + self.pass,
            username: "" + self.name
          })
        })
          .then(function(response) {
            if (response.status == 400) {
              console.log(
                "Looks like there was a problem. Status Code: " +
                  response.status
              );
              self.alert = true;
              self.loading = false;
              //self.reset();
              return;
            }

            // Examine the text in the response
            if (response.status == 200)
              response.json().then(function(data) {
                console.log(data);
                // localStorage.setItem("token", data.token);
                // localStorage.setItem("name", data.data.name);
                // localStorage.setItem("id", data.data._id);
                // localStorage.setItem("secret", data.permission[0]);
                // localStorage.setItem("topSecret", data.permission[1]);
                // self.$router.push({
                //   name: "dashboard"
                // });
                // sharedData.data.isLoggedIn = true;
                //console.log(sharedData.data.isLoggedIn);
                self.loading = false;
                self.alert2 = true;
              });
          })
          .catch(function(err) {
            console.log("Fetch Error :-S", err);
            console.log(self.name, self.pass);
            console.log(
              "error occured wile trying to connect refresh to continue"
            );
            self.alert = true;
            self.loading = false;
          });
      }
    },
    reset() {
      this.$refs.form.reset();
    },
    resetValidation() {
      this.$refs.form.resetValidation();
    }
  }
};
</script>

