<template>
  <div id="app">
    <v-app id="inspire">
      <v-content>
        <v-container v-if="loading" fluid fill-height>
          <!-- <div class="text-xs-center"> -->
          <v-layout align-center justify-center>
            <v-progress-circular
              v-if="loading"
              :size="70"
              :width="7"
              color="indigo lighten-1"
              indeterminate
            ></v-progress-circular>
            <!-- </div> -->
          </v-layout>
        </v-container>
        <!-- <div v-if="!loading"> -->
        <v-container v-if="!loading" fluid fill-height>
          <v-layout align-center="align-center" justify-center="justify-center">
            <v-flex class="login-form text-xs-center">
              <v-card light="light">
                <v-card-text>
                  <div class="display-1 mb-3">
                    <!-- <v-icon class="mr-2" large="large">work</v-icon> -->
                    <v-avatar size="65px" color="info">
                      <v-icon x-large color="white">person</v-icon>
                    </v-avatar>
                  </div>
                  <v-form ref="form" v-model="valid" lazy-validation>
                    <v-alert
                      :value="alert"
                      color="error"
                      icon="new_releases"
                    >wrong info or bad connection.</v-alert>
                    <v-text-field
                      light="light"
                      prepend-icon="person"
                      v-model="name"
                      type="text"
                      :counter="30"
                      :rules="eRules"
                      label="Name"
                      required
                    ></v-text-field>
                    <v-text-field
                      light="light"
                      prepend-icon="lock"
                      name="password"
                      label="Password"
                      v-model="pass"
                      id="password"
                      type="password"
                      :counter="10"
                      :rules="passRules"
                      required
                    ></v-text-field>
                    <v-btn
                      color="info"
                      class="white--text"
                      :disabled="!valid"
                      @click="validate"
                    >Sign in</v-btn>
                  </v-form>
                </v-card-text>
              </v-card>
              <div style="margin-top:13px;">
                Don't have an account?
                <router-link :to="{name:'signup'}">sign up</router-link>
              </div>
            </v-flex>
          </v-layout>
        </v-container>
        <!-- </div> -->
      </v-content>
    </v-app>
  </div>
</template>

<script>
export default {
  data: () => ({
    valid: true,
    name: "",
    pass: "",
    eRules: [
      v => !!v || "Name is required",
      v => (v && v.length <= 30) || "Name must be less than 30 characters"
    ],
    passRules: [
      v => !!v || "password is required",
      v => (v && v.length <= 10) || "password must be less than 10 characters"
    ],
    alert: false,
    loading: false
  }),
  methods: {
    async validate() {
      var self = this;
      if (this.$refs.form.validate()) {
        self.loading = true;
        this.snackbar = true;
        console.log(this.name, this.pass);
        //http://localhost:8000/
        //https://quiet-lake-60566.herokuapp.com/login
        await fetch("http://localhost:9000/auth", {
          method: "POST",
          headers: {
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            username: "" + this.name,
            password: "" + this.pass
          })
        })
          .then(function(response) {
            if (response.status !== 200) {
              console.log(
                "Looks like there was a problem. Status Code: " +
                  response.status
              );
              self.alert = true;
              self.loading = false;
              self.reset();
              return;
            }
            // Examine the text in the response
            if (response.status == 200)
              response.json().then(function(data) {
                console.log(data);
                localStorage.setItem("token", data.data["token"]);
                localStorage.setItem("username", data.data["username"]);
                self.$router.push({
                  name: "home"
                });
                // sharedData.data.isLoggedIn = true;
                //console.log(sharedData.data.isLoggedIn);
              });
          })
          .catch(function(err) {
            console.log("Fetch Error :-S", err);
            console.log(self.email, self.pass);
            console.log(
              "error occured wile trying to connect refresh to continue"
            );
            self.alert = true;
            self.loading = false;
          });
      }
    },
    reset() {
      this.$refs.form.reset();
    },
    resetValidation() {
      this.$refs.form.resetValidation();
    }
  }
};
</script>

